import React from "react";

const Pictures = () => {
  const picArr = ["IMG_4029.JPG", "IMG_4030.JPG", "IMG_4031.JPG"];
  const path = "%PUBLIC_URL%/../src/img/";

  return (
    <div className="picture_div">
      <img class="pictures" src={path + picArr[0]} />
      <img class="pictures" src={path + picArr[1]} />
      <img class="pictures" src={path + picArr[2]} />
    </div>
  );
};

export default Pictures;
